html, body {
  height: 100%;
  width: 100%;
  overflow: hidden;
  box-sizing: border-box;
}

body {
  padding: 0;
  margin: 0;
  background: #333;
}
#outer-wrapper {
  -webkit-overflow-scrolling: touch;
  height: 100%;
  overflow: auto;
}
canvas {
  position: fixed;
  top: 2px;
  width: 100%;
  height: 99.5%;
  padding: 0;
  margin: 0;
}